.storyE {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: url("./storyE_background.png") no-repeat center;
  background-size: cover;
  overflow: hidden;
  min-width: 1440px;
  min-height: 688px;
}

.storyE-background {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  z-index: 2;
  pointer-events: none;
}

.storyE .sound {
  position: absolute;
  bottom: 10%;
  right: 5%;
}

.patternG {
  z-index: 5;
  position: absolute;
  top: 68.85%;
  left: 20%;
  width: 3%;
  -webkit-animation-name: patternG-move;
          animation-name: patternG-move;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.patternG:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

@-webkit-keyframes patternG-move {
  from {
    -webkit-transform: rotate(0deg) translateX(10px) rotate(0deg);
            transform: rotate(0deg) translateX(10px) rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg) translateX(10px) rotate(-360deg);
            transform: rotate(360deg) translateX(10px) rotate(-360deg);
  }
}

@keyframes patternG-move {
  from {
    -webkit-transform: rotate(0deg) translateX(10px) rotate(0deg);
            transform: rotate(0deg) translateX(10px) rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg) translateX(10px) rotate(-360deg);
            transform: rotate(360deg) translateX(10px) rotate(-360deg);
  }
}

.boyF {
  position: absolute;
  top: 16.8%;
  right: 41.78%;
  width: 14.34%;
  z-index: 3;
}

.patternH {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 11.4%;
  right: 43.39%;
  width: 2.8%;
  z-index: 5;
}

.patternH.show {
  visibility: visible;
  -webkit-animation-name: patternH-show-opacity, patternH-show-scale;
          animation-name: patternH-show-opacity, patternH-show-scale;
  -webkit-animation-duration: 1s, 1s;
          animation-duration: 1s, 1s;
  -webkit-animation-timing-function: ease-in-out, ease-in-out;
          animation-timing-function: ease-in-out, ease-in-out;
  -webkit-animation-fill-mode: forwards, forwards;
          animation-fill-mode: forwards, forwards;
}

@-webkit-keyframes patternH-show-opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes patternH-show-opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes patternH-show-scale {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes patternH-show-scale {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.carrots {
  position: absolute;
  top: 51.44%;
  right: 18.38%;
  width: 59.3%;
  z-index: 4;
  pointer-events: none;
}

.questionC {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.questionC-mask {
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0a0a0f;
  opacity: 0.8;
}

.buttonHI {
  z-index: 101;
  position: absolute;
  width: 22.55%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  visibility: hidden;
}

.buttonH {
  top: 32.84%;
}

.buttonI {
  top: 53.98%;
}

.buttonHI.show-enter,
.buttonHI.show-enter-active,
.buttonHI.show-enter-done,
.buttonHI.show-exit,
.buttonHI.show-exit-active {
  visibility: visible;
}

.buttonHI.show-exit-done {
  visibility: hidden;
}

.buttonHI-text {
  width: 100%;
  visibility: hidden;
  position: relative;
  z-index: 3;
}

.buttonHI-text.show-enter {
  -webkit-animation-name: buttonHI-text-show;
          animation-name: buttonHI-text-show;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
  -webkit-animation-duration: 1ms;
          animation-duration: 1ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes buttonHI-text-show {
  from {
    visibility: hidden;
  }

  to {
    visibility: visible;
  }
}

@keyframes buttonHI-text-show {
  from {
    visibility: hidden;
  }

  to {
    visibility: visible;
  }
}

.buttonHI-text.show-enter-done {
  visibility: visible;
}

.buttonHI-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
}

.buttonHI-background.zoom-enter {
  -webkit-animation-name: buttonHI-background-show;
          animation-name: buttonHI-background-show;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes buttonHI-background-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes buttonHI-background-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



