.original-painting-dialog-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  min-width: 1440px;
  min-height: 688px;
  opacity: 0;
  visibility: hidden;
}

.original-painting-dialog-wrapper.show-enter,
.original-painting-dialog-wrapper.show-enter-active,
.original-painting-dialog-wrapper.show-enter-done,
.original-painting-dialog-wrapper.show-exit,
.original-painting-dialog-wrapper.show-exit-active {
  opacity: 1;
  visibility: visible;
}

.original-painting-dialog-wrapper.show-exit-done {
  opacity: 0;
  visibility: hidden;
}

.original-painting-dialog-mask {
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #3c3732;
  opacity: 0.7;
}

.close-original-painting-dialog-button {
  z-index: 102;
  position: absolute;
  top: 8.67%;
  right: 7.1%;
  width: 3.43%;
}

.close-original-painting-dialog-button:hover {
  -webkit-animation-name: close-original-painting-dialog-button-hover;
          animation-name: close-original-painting-dialog-button-hover;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes close-original-painting-dialog-button-hover {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes close-original-painting-dialog-button-hover {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.close-original-painting-dialog-button.zoom-exit {
  -webkit-animation-name: close-original-painting-dialog-button-hide;
          animation-name: close-original-painting-dialog-button-hide;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes close-original-painting-dialog-button-hide {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
  }
}

@keyframes close-original-painting-dialog-button-hide {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
  }
}

.original-painting-dialog {
  z-index: 101;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: 45%;
}

.original-painting-dialog.zoom-enter {
  -webkit-animation-name: original-painting-dialog-show;
          animation-name: original-painting-dialog-show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes original-painting-dialog-show {
  0% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0.8);
            transform: translateX(-50%) translateY(-50%) scale(0.8);
    opacity: 0;
  }

  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
    opacity: 1;
  }
}

@keyframes original-painting-dialog-show {
  0% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0.8);
            transform: translateX(-50%) translateY(-50%) scale(0.8);
    opacity: 0;
  }

  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
    opacity: 1;
  }
}

.original-painting-dialog.zoom-exit {
  -webkit-animation-name: original-painting-dialog-hide;
          animation-name: original-painting-dialog-hide;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes original-painting-dialog-hide {
  0% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0.8);
            transform: translateX(-50%) translateY(-50%) scale(0.8);
    opacity: 0;
  }
}

@keyframes original-painting-dialog-hide {
  0% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0.8);
            transform: translateX(-50%) translateY(-50%) scale(0.8);
    opacity: 0;
  }
}

.original-painting-dialog-picture {
  width: 100%;
  height: 100%;
}



